import React, { useState, useEffect } from "react";
import "./style.scss";
import routeMain from "./routes";

//redux
import { useDispatch, useSelector } from "react-redux";
import { loadNews } from "store/news/actions";
import { selectList } from "store/news/selector";

import NewsList from "components/NewsList";
import PreloaderProgress from "components/preloader";

import PageTitle from "components/PageTitle";
import useStyles from "MaterialStyles/styles";

import clsx from "clsx";

const NewsListPage = () => {
	// const [loading, setLoading] = useState(false);
	const classes = useStyles();
	const [progress, setProgress] = React.useState(10);

	const dispatch = useDispatch();
	const getNewsList = useSelector(selectList);

	useEffect(() => {
		dispatch(loadNews());
		//   setLoading(true);
		// setTimeout(() => {
		// 	setLoading(false);
		// }, 3000);
	}, [dispatch]);

	useEffect(() => {
		const timer = setInterval(() => {
			setProgress(prevProgress => (prevProgress >= 100 ? 0 : prevProgress + 10));
		}, 800);
		return () => {
			clearInterval(timer);
		};
	}, []);

	return (
		<section className={clsx(classes.preloader, "News")}>
			{getNewsList.length === 0 ? (
				<PreloaderProgress value={progress} />
			) : (
				<>
					{" "}
					<PageTitle
						title={
							<>
								Будь в курсе <span style={{ color: "blue" }}>событий</span>{" "}
							</>
						}></PageTitle>
					{getNewsList.length > 0 && <NewsList  list={getNewsList} />}
				</>
			)}
		</section>
	);
};
export { routeMain };

export default NewsListPage;
