import React from "react";

import routeMain from "./routes";

import photoKot from "assets/img/Group-5.png";
import { Grid } from "@mui/material";

import useStyles from "MaterialStyles/styles";

const Contacts = () => {
	const classes = useStyles();
	return (
		<section className='contacts'>
			<Grid container className={classes.contacts__block}>
				<Grid item md={6} style={{ display: "flex" }}>
					<div className={classes.contacts__content}>
						<a href='tel:+79991234576'>+7 (918) 630 45 76</a>
						<p>
							Константин <br /> Заярский
						</p>
						<a href='mailto:kostja-kot@mail.ru'>kostja-kot@mail.ru</a>
						<p> JavaScript Developer</p>
						<p>
							ES5,ES6,
							<span className='react' style={{ color: "blue" }}>
								React
							</span>
						</p>
					</div>
				</Grid>
				<Grid item md={6}>
					<div className={classes.contacts__img}>
						<img src={photoKot} alt='photoKot' />
					</div>
				</Grid>
			</Grid>
		</section>
	);
};

export { routeMain };

export default Contacts;
