import React, { useState, useEffect } from "react";


import routeMain from "./routes";

import {ID} from "types/ID"
import {INewsDetail} from "types/INewsDetail"

import { useParams } from "react-router-dom";

import getNews from "services/getNews";
import DateView from "components/DateView";

//redux
import { useSelector } from "react-redux";
import { selectList } from "store/news/selector";

import { Grid } from "@mui/material";
import { css } from "@emotion/react";
import HashLoader from "react-spinners/HashLoader";
import Typography from "@mui/material/Typography";
import useStyles from "MaterialStyles/styles";

  

const override = css`
	display: block;
	margin: 0 auto;
	border-color: red;
`;

const NewsDetail = () => {
	const classes = useStyles();

	const { id } = useParams<ID>();
	const [news, setNews] = useState<INewsDetail | undefined>(undefined);

  const getNewsList = useSelector(selectList);

	useEffect(() => {
      const currentNews = getNewsList?.find((item: INewsDetail) => item._id === id)
			setNews(currentNews);
	}, [id, getNewsList]);

	return (
		<section className={'newsDetail'}>
			{news === undefined ? (
				<HashLoader css={override} color={"#36D7B7"}  size={100} />
			) : (
				<Grid container className={classes.newsDetail}>
					<Grid item md={6} className={classes.newsDetail__block}>
						<Typography variant='h4' className='newsDetail__title'>
							{news.title}
						</Typography>
						<Typography variant='body1' className='newsDetail__source'>
							{news.clean_url}
						</Typography>
						<DateView value={news.published_date} />
					</Grid>
					<Grid item md={6} className={classes.newsDetail__block}>
						<img className={classes.newsDetail__summary} src={news.media} alt={news.media} />
						<Typography variant='body1' className='newsDetail__summary'>
							{news.summary}
						</Typography>
					</Grid>
				</Grid>
			)}
		</section>
	);
};
export { routeMain };

export default NewsDetail;
