import React from "react";

import Card from "@mui/material/Card";

import { NavLink } from "react-router-dom";
import { routeMain as routeNewsDetail } from "pages/NewsDetail";

import CardContent from "@mui/material/CardContent";
import DateView from "components/DateView";

import {INewsDetail} from "types/INewsDetail";

import "./style.scss";

interface INewsItemParams {
	item:  INewsDetail;
}

const NewsItem: React.FC<INewsItemParams> = ({item}) => {
	return (
		<Card className='newsCard' variant='outlined' style={{ maxWidth: 310,width: "100%", minHeight: 200 }}>
			<NavLink to={routeNewsDetail(item._id)}>
				<CardContent className='newsCard__content'>
					<p>{item.title}</p>
					<div className='newsCard__link-data'>
						<p className='newsCard__link' color='text.secondary'>
							{item.clean_url}
						</p>
						<DateView value={item.published_date} />
					</div>
				</CardContent>
			</NavLink>
		</Card>
	);
};

export default NewsItem;
